
import React, { PureComponent } from 'react';
import a from './pokemon.scss';

// 자기 소개
class Pokemon extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            contentCode: ''
        }
    }
    render(){
        return (
            <div className="pop_content">
                <div className="pop_preview">
                {/* <audio autoplay=''>
                    <source src='https://s3-us-west-2.amazonaws.com/s.cdpn.io/142927/intro.mp3' type='audio/mpeg'>
                </audio> */}
                    <div class='wrapper'>
                        <div class='composition'>
                            <div class='scene scene--1'>
                                <div class='shooting-star'></div>
                                <div class='gamefreak-logo'></div>
                                <div class='gamefreak'></div>
                                <div class='star-shower-mask'>
                                    <div class='star-shower--light'></div>
                                    <div class='star-shower--dark'></div>
                                </div>
                            </div>
                            <div class='scene scene--2'>
                                <section class='opponent jigglypuff-wrapper'>
                                    <figure class='jigglypuff'></figure>
                                    <figure class='jigglypuff--flinch'></figure>
                                    <figure class='jigglypuff--tackle'></figure>
                                </section>
                                <section class='opponent nidorino-wrapper'>
                                    <figure class='nidorino'></figure>
                                    <figure class='nidorino--flinch'></figure>
                                    <figure class='nidorino--tackle'></figure>
                                </section>
                                <section class='gengar-wrapper'>
                                    <figure class='gengar'></figure>
                                    <figure class='gengar--scratch1'></figure>
                                    <figure class='gengar--scratch2'></figure>
                                </section>
                            </div>
                            <div class='scene scene--3'>
                                <div class='logo-wrapper'>
                                    <figure class='pokemon-logo'></figure>
                                    <span>FactAttacker</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Pokemon;
